"use client";

import React, { useState } from 'react';
import { useParams } from 'next/navigation';
import { getProductById } from '@/lib/products';
import { useCart } from '@/context/CartContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import Link from 'next/link';

export default function ProductDetailPage() {
  const params = useParams();
  const productId = params.id as string;
  const { addToCart } = useCart();
  
  const [selectedSize, setSelectedSize] = useState<string>('');
  const [selectedColor, setSelectedColor] = useState<string>('');
  const [quantity, setQuantity] = useState<number>(1);
  const [isAddingToCart, setIsAddingToCart] = useState<boolean>(false);
  const [addToCartMessage, setAddToCartMessage] = useState<string>('');

  let product;
  try {
    product = getProductById(productId);
  } catch (error) {
    console.error('Error fetching product:', error);
  }

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-16 text-center">
        <h1 className="text-2xl font-bold mb-4">Product Not Found</h1>
        <p className="text-muted-foreground mb-8">
          Sorry, we couldn't find the product you're looking for.
        </p>
        <Link href="/">
          <Button>Return to Home</Button>
        </Link>
      </div>
    );
  }

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    e.currentTarget.src = 'https://placehold.co/600x600?text=Product+image+unavailable';
  };

  const handleAddToCart = async () => {
    if (!selectedSize || !selectedColor) {
      setAddToCartMessage('Please select size and color');
      setTimeout(() => setAddToCartMessage(''), 3000);
      return;
    }

    setIsAddingToCart(true);
    
    try {
      await new Promise(resolve => setTimeout(resolve, 500)); // Simulate loading
      
      addToCart({
        ...product,
        quantity,
        selectedSize,
        selectedColor
      });
      
      setAddToCartMessage('Added to cart successfully!');
      setTimeout(() => setAddToCartMessage(''), 3000);
    } catch (error) {
      console.error('Error adding to cart:', error);
      setAddToCartMessage('Failed to add to cart. Please try again.');
      setTimeout(() => setAddToCartMessage(''), 3000);
    } finally {
      setIsAddingToCart(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Breadcrumb */}
      <nav className="mb-8">
        <div className="flex items-center space-x-2 text-sm text-muted-foreground">
          <Link href="/" className="hover:text-foreground">Home</Link>
          <span>/</span>
          <Link href="/shop" className="hover:text-foreground">Shop</Link>
          <span>/</span>
          <span className="text-foreground">{product.name}</span>
        </div>
      </nav>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
        {/* Product Image */}
        <div className="space-y-4">
          <div className="aspect-square overflow-hidden rounded-lg border">
            <img
              src={product.image}
              alt={`${product.name} - ${product.description}`}
              className="w-full h-full object-cover"
              onError={handleImageError}
            />
          </div>
        </div>

        {/* Product Details */}
        <div className="space-y-6">
          <div>
            <h1 className="text-3xl font-bold mb-2">{product.name}</h1>
            <p className="text-2xl font-bold text-primary">${product.price}</p>
            <span className="inline-block bg-secondary text-secondary-foreground px-3 py-1 rounded-full text-sm mt-2">
              {product.category}
            </span>
          </div>

          <div>
            <h3 className="font-semibold mb-2">Description</h3>
            <p className="text-muted-foreground leading-relaxed">
              {product.description}
            </p>
          </div>

          {/* Size Selection */}
          <div>
            <h3 className="font-semibold mb-3">Size</h3>
            <Select value={selectedSize} onValueChange={setSelectedSize}>
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Select a size" />
              </SelectTrigger>
              <SelectContent>
                {product.sizes.map((size) => (
                  <SelectItem key={size} value={size}>
                    {size}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Color Selection */}
          <div>
            <h3 className="font-semibold mb-3">Color</h3>
            <Select value={selectedColor} onValueChange={setSelectedColor}>
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Select a color" />
              </SelectTrigger>
              <SelectContent>
                {product.colors.map((color) => (
                  <SelectItem key={color} value={color}>
                    {color}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Quantity */}
          <div>
            <h3 className="font-semibold mb-3">Quantity</h3>
            <div className="flex items-center space-x-3">
              <Button
                variant="outline"
                size="icon"
                onClick={() => setQuantity(Math.max(1, quantity - 1))}
                disabled={quantity <= 1}
              >
                -
              </Button>
              <span className="w-12 text-center font-medium">{quantity}</span>
              <Button
                variant="outline"
                size="icon"
                onClick={() => setQuantity(quantity + 1)}
              >
                +
              </Button>
            </div>
          </div>

          {/* Add to Cart Button */}
          <div className="space-y-4">
            <Button
              onClick={handleAddToCart}
              disabled={isAddingToCart || !selectedSize || !selectedColor}
              className="w-full"
              size="lg"
            >
              {isAddingToCart ? 'Adding to Cart...' : 'Add to Cart'}
            </Button>
            
            {addToCartMessage && (
              <p className={`text-sm text-center ${
                addToCartMessage.includes('success') 
                  ? 'text-green-600' 
                  : 'text-red-600'
              }`}>
                {addToCartMessage}
              </p>
            )}
          </div>

          {/* Product Features */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Product Features</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              <div className="flex justify-between">
                <span className="text-muted-foreground">Category:</span>
                <span>{product.category}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Available Sizes:</span>
                <span>{product.sizes.join(', ')}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Available Colors:</span>
                <span>{product.colors.join(', ')}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Free Shipping:</span>
                <span>On orders over $75</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Returns:</span>
                <span>30-day return policy</span>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
